@import "variables";

.card {
  width: 200px;
  padding: 30px 14px 15px;
  margin-bottom: 50px;
  font-size: $small-font-size;
  line-height: 15px;
  color: $text-card-color;
  border: 1px solid $border-card-color;
  margin-right: 48px;

  &:nth-child(4n) {
    margin-right: 0;
  }

  &__image {
    display: block;
    width: 160px;
    height: 134px;
    margin: auto;
    margin-bottom: 45px;
  }

  &__title {
    margin-bottom: 4px;
    font-size: $middle-font-size;
    line-height: 15px;
    font-weight: 500;
  }

  &__code {
    margin-bottom: 14px;
  }

  &__review {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    margin-bottom: 18px;
    line-height: 19px;
  }

  &__price-name {
    margin-top: 2px;
  }

  &__price-value {
    font-size: 16px;
    font-weight: 700;
  }

  &__buy {
    width: 100%;
    font-family: $main-font-family;
    font-weight: bold;
    font-size: $big-font-size;
    color: $white-color;
    text-transform: uppercase;
    line-height: 36px;
    border: 1px solid $main-color;
    border-radius: 5px;
    background-color: $main-color;
    transition: all 300ms ease;
    cursor: pointer;
  }

  &__buy:hover {
    color: $main-color;
    background-color: $white-color;
  }
}

@media (max-width: 1023px) {
  .card:nth-child(4n) {
    margin-right: 48px;
  }

  .card:nth-child(3n) {
    margin-right: 0;
  }
}

@media (max-width: 780px) {
  .card:nth-child(3n) {
    margin-right: 48px;
  }

  .card:nth-child(2n) {
    margin-right: 0;
  }
}

@media (max-width:530px) {
  .card {
    margin-right: 0;
  }
}
